@font-face {
  font-family: "NotoSansHans-Bold";
  src: url(/static/media/NotoSansHans-Bold1.1a1d2b4f.ttf), url(/static/media/NotoSansHans-Bold.be1ab001.eot);
}
@font-face {
  font-family: "NotoSansHans-Regular";
  src: url(/static/media/NotoSans-Regular1.0c22b1d8.ttf), url(/static/media/NotoSansHans-Regular.127a1713.eot);
}
@font-face {
  font-family: "NotoSansHans-Medium";
  src: url(/static/media/NotoSansHans-Medium.1d9e1675.ttf), url(/static/media/NotoSansHans-Medium.e257c7c1.eot);
}
.ant-upload.ant-upload-drag .ant-upload {
  padding: 16px 16px !important;
}
.pannelWithConvertType {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.draggerPannelCommon {
  height: 340px;
  position: relative;
}
.draggerPannelCommon .ant-upload.ant-upload-drag:hover {
  border: 3px solid #E6E6E6 !important;
}
.pannelWithoutUpload {
  border: 3px solid #E6E6E6;
  height: 340px;
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.uploading .uploadImage {
  width: 103px;
  height: 76px;
  background: url("http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/uploading.png") no-repeat center;
  background-size: contain;
  margin: 0 auto;
  margin-bottom: 30px;
}
.uploading span {
  line-height: 16px;
  font-family: "NotoSansHans-Medium", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 16px;
  color: #EB6331;
  letter-spacing: 0;
  text-align: center;
}
.uploading .dot {
  display: inline-block;
  width: 3ch;
  text-indent: -1ch;
  vertical-align: bottom;
  overflow: hidden;
  -webkit-animation: dot 3s infinite step-start both;
          animation: dot 3s infinite step-start both;
  color: #EB6331;
  /*  */
  font-family: monospace;
}
@-webkit-keyframes dot {
  33% {
    text-indent: 0;
  }
  66% {
    text-indent: -2ch;
  }
}
@keyframes dot {
  33% {
    text-indent: 0;
  }
  66% {
    text-indent: -2ch;
  }
}
.errorFailImage {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 80%;
}
.CSVFileImage {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 180px;
  margin-bottom: 25px;
}
.CSVFileImage .fileName {
  max-width: 140px;
}
.iconFailed {
  width: 79px;
  height: 79px;
  background: url("http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/failed.png") no-repeat center;
  background-size: contain;
  margin: 10px auto;
}
.csvIconBlack {
  width: 74px;
  height: 88px;
  background: url("http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/csv_black.png") no-repeat top;
  background-size: contain;
}
.csvIconWhite {
  width: 86px;
  height: 75px;
  background: url("http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/csv_white.png") no-repeat center;
  background-size: contain;
  margin: 0 auto;
}
.uploadView .uploadBtn {
  width: 200px;
  height: 50px;
  background: #FFFFFF;
  box-shadow: 0 2px 3px 0 #D09613;
  border-radius: 5px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  font-family: "NotoSansHans-Medium", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 16px;
  color: #000000;
  letter-spacing: 0;
  text-align: center;
  margin-bottom: 19px;
}
.uploadView .uploadBtn div:nth-child(1) {
  width: 20px;
  height: 23px;
  background: url("http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/file.png") no-repeat center;
  background-size: contain;
  margin-right: 10px;
}
.uploadView .uploadBtn span {
  font-family: "NotoSansHans-Medium", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 14px;
  color: #FFFFFF;
  letter-spacing: 0;
  text-align: center;
}
.uploadView span:nth-child(2) {
  font-family: "NotoSansHans-Medium", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 14px;
  color: #FFFFFF;
  letter-spacing: 0;
  text-align: center;
}
.pannelContent {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 300px;
}
.pannelContent .largeFileView {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-family: "NotoSansHans-Medium", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 16px;
  color: #000000;
  letter-spacing: 0;
  text-align: center;
}
.pannelContent .largeFileView div {
  width: 40px;
  height: 37px;
  min-width: 40px;
  min-height: 37px;
  margin-right: 17px;
  background: url("http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/warning.png") no-repeat center;
  background-size: cover;
}
.pannelContent .middlePannelView {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  position: relative;
}
.pannelContent .middlePannelView .fileNametext {
  max-width: 150px;
  line-height: 20px !important;
  font-family: "NotoSansHans-Medium", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 16px;
  color: #000000;
  letter-spacing: 0;
  text-align: center;
  height: 40px;
  max-height: 40px;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
  word-break: break-all;
}
.pannelContent .hintText {
  font-family: "NotoSansHans-Medium", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 16px;
  color: #000000;
  letter-spacing: 0;
  text-align: center;
  line-height: 26px;
}
.pannelContent .ClickToUpload:hover {
  border: #FCBE31 4px solid;
}
.pannelContent .ClickToUpload {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 188px;
  height: 45px;
  background-color: #064969;
  cursor: pointer;
  margin-top: 30px;
}
.pannelContent .ClickToUpload div {
  width: 21px;
  height: 21px;
  margin-right: 11px;
  background: url("http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/upload_white.png") no-repeat center;
  background-size: contain;
}
.pannelContent .ClickToUpload p {
  font-family: "NotoSansHans-Medium", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 16px;
  color: #FFFFFF;
  letter-spacing: 0;
  text-align: center;
  margin-bottom: 0px;
}
.arrowLineDiv {
  font-family: "NotoSansHans-Medium", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 16px;
  color: #EB6331;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.arrowLineDiv p {
  padding: 6px 0px;
  margin-bottom: 0px !important;
}
.arrowLine {
  position: relative;
  width: 84px;
  padding-right: 30px;
  height: 3px;
  background-color: #EB6331;
}
.arrowLine:after {
  margin-right: -10px;
  margin-top: -6px;
  border: 8px solid transparent;
  border-left: 8px solid #EB6331;
  width: 0;
  height: 0;
  position: absolute;
  right: 0px;
  content: ' ';
}
.yellowPannel .ant-upload.ant-upload-drag {
  background-color: #FFDB88 !important;
}
.yellowPannel .ant-upload-drag-container {
  background-color: #FCBE31;
  border: dashed 2px white;
}
.whitePannel .ant-upload.ant-upload-drag {
  background-color: white;
  border: 3px solid #E6E6E6;
}
.whitePannel .ant-upload-drag-container {
  background-color: white;
}
.ant-progress-inner,
.ant-progress-bg {
  border-radius: 0px !important;
}
