@font-face {
  font-family: "NotoSansHans-Bold";
  src: url(/static/media/NotoSansHans-Bold1.1a1d2b4f.ttf), url(/static/media/NotoSansHans-Bold.be1ab001.eot);
}
@font-face {
  font-family: "NotoSansHans-Regular";
  src: url(/static/media/NotoSans-Regular1.0c22b1d8.ttf), url(/static/media/NotoSansHans-Regular.127a1713.eot);
}
@font-face {
  font-family: "NotoSansHans-Medium";
  src: url(/static/media/NotoSansHans-Medium.1d9e1675.ttf), url(/static/media/NotoSansHans-Medium.e257c7c1.eot);
}
.draggerPannelCommon {
  height: 340px;
  position: relative;
}
.draggerPannelCommon .ant-upload.ant-upload-drag:hover {
  border: 3px solid #E6E6E6 !important;
}
.VHCenter {
  display: flex;
  flex-direction: row;
}
.pannel-box {
  border: 3px solid #E6E6E6;
  height: 340px;
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.convertingTypeImage {
  width: 180px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.convertingTypeImage span {
  font-family: "NotoSansHans-Medium", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 16px;
  color: #000000;
  letter-spacing: 0;
  text-align: center;
}
.fileName {
  max-width: 120px;
  line-height: 20px !important;
  font-family: "NotoSansHans-Medium", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 16px;
  color: #000000;
  letter-spacing: 0;
  text-align: center;
  height: 40px;
  max-height: 40px;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
  word-break: break-all;
}
.hintText {
  font-family: "NotoSansHans-Medium", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 16px;
  color: #000000;
  letter-spacing: 0;
  text-align: center;
  line-height: 26px;
}
.ClickToUpload:hover {
  border: #FCBE31 4px solid;
}
.ClickToUpload {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 188px;
  height: 45px;
  background-color: #064969;
  cursor: pointer;
}
.ClickToUpload div {
  width: 21px;
  height: 21px;
  margin-right: 11px;
  background: url("http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/upload_white.png") no-repeat center;
  background-size: contain;
}
.ClickToUpload p {
  font-family: "NotoSansHans-Medium", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 16px;
  color: #FFFFFF;
  letter-spacing: 0;
  text-align: center;
  margin-bottom: 0px;
}
.bottomPannelView {
  margin-top: 27px;
}
.bottomPannelView .DownloadView .ant-col:hover {
  border: #FCBE31 4px solid;
}
.bottomPannelView .DownloadView .ant-col {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 140px;
  height: 45px;
  background-color: #064969;
  cursor: pointer;
}
.bottomPannelView .DownloadView .ant-col div {
  width: 21px;
  height: 21px;
  margin-right: 11px;
}
.bottomPannelView .DownloadView .ant-col p {
  font-family: "NotoSansHans-Medium", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 16px;
  color: #FFFFFF;
  letter-spacing: 0;
  text-align: center;
  margin-bottom: 0px;
}
.VHCenter .GenerateIcon {
  background-repeat: no-repeat;
  background-position: center;
  width: 53px;
  height: 63px;
  background-size: contain;
  margin-bottom: 20px;
}
.ConvertFormsDiv {
  height: 340px;
  -webkit-column-gap: 2px;
  /* Chrome, Safari, Opera */
  -moz-column-gap: 2px;
  /* Firefox */
  grid-column-gap: 2px;
  column-gap: 2px;
}
.ConvertFormsDiv .convert-box:hover {
  border: #FCBE31 5px solid;
}
.ConvertFormsDiv .convert-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border: 1px solid #FFFFFF;
  padding: 7px;
}
.ConvertFormsDiv .icon {
  background-repeat: no-repeat;
  background-position: center;
  width: 53px;
  height: 63px;
  background-size: contain;
  margin-bottom: 11px;
}
.ConvertFormsDiv .icon + span {
  font-family: "NotoSansHans-Medium", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 18px;
  color: #FFFFFF;
  text-align: center;
}
.ant-progress-inner,
.ant-progress-bg {
  border-radius: 0px !important;
}
@media screen and (min-width: 1300px) {
  .convert-box {
    width: 170px;
  }
}
@media screen and (min-width: 1080px) and (max-width: 1300px) {
  .convert-box {
    width: 150px;
  }
  .convert-box .icon {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
}
@media screen and (min-width: 880px) and (max-width: 1080px) {
  .convert-box {
    width: 140px;
  }
  .convert-box .icon {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
  .convert-box span {
    font-size: 17px !important;
  }
}
@media screen and (min-width: 575px) and (max-width: 880px) {
  .pannel-box .CSVFileImage .csvIconBlack {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
  .convert-box {
    width: 120px;
  }
  .convert-box .icon {
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
  }
  .convert-box span {
    font-size: 16px !important;
  }
}
@media screen and (min-width: 524px) and (max-width: 575px) {
  .pannel-box .CSVFileImage .csvIconBlack {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
  .convert-box {
    width: 120px;
  }
  .convert-box .icon {
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
  }
  .convert-box span {
    font-size: 15px !important;
  }
}
@media screen and (min-width: 400px) and (max-width: 524px) {
  .pannel-box .CSVFileImage .csvIconBlack {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
  .convert-box {
    width: 100px;
  }
  .convert-box .icon {
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
  }
  .convert-box span {
    font-size: 14px !important;
  }
}
@media screen and (max-width: 400px) {
  .pannel-box .CSVFileImage .csvIconBlack {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
  .convert-box {
    width: 100px;
  }
  .convert-box .icon {
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
  }
  .convert-box span {
    font-size: 14px !important;
  }
}
