@font-face {
  font-family: "NotoSansHans-Bold";
  src: url(/static/media/NotoSansHans-Bold1.1a1d2b4f.ttf), url(/static/media/NotoSansHans-Bold.be1ab001.eot);
}
@font-face {
  font-family: "NotoSansHans-Regular";
  src: url(/static/media/NotoSans-Regular1.0c22b1d8.ttf), url(/static/media/NotoSansHans-Regular.127a1713.eot);
}
@font-face {
  font-family: "NotoSansHans-Medium";
  src: url(/static/media/NotoSansHans-Medium.1d9e1675.ttf), url(/static/media/NotoSansHans-Medium.e257c7c1.eot);
}
.headerBarWrapper {
  width: 100%;
  height: 50px;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.headerBarWrapper .headerBarLeft {
  height: 50px;
  display: flex;
  flex-direction: row;
  position: relative;
  align-items: center;
}
.headerBarWrapper .headerBarLeft span {
  font-family: "NotoSansHans-Medium", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 22px;
  line-height: 22px;
  color: #000000;
  text-align: left;
  margin-left: 5px;
  white-space: nowrap;
}
.headerBarWrapper .headerBarLeft div {
  margin-left: 30.1px;
  margin-right: 11.1px;
  height: 53px;
  width: 53px;
  background: url('http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/headbar/logo1.png') left no-repeat;
  background-size: contain;
}
.headerBarWrapper .headerBarRight {
  height: 50px;
  flex-direction: row;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-right: 10px;
}
.headerBarWrapper .headerBarRight .ant-divider-vertical {
  height: 24px;
  width: 2px;
  background-color: black;
  margin: 0px 16px;
}
.headerBarWrapper .headerBarRight .defaultimg {
  height: 26px;
  width: 23px;
  background: url('http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/headbar/user.png') center no-repeat;
  background-size: contain;
}
.headerBarWrapper .headerBarRight .AvatarImg {
  height: 26px;
  width: 26px;
  background-size: contain;
  background: #ffe3b1;
  color: #38291f;
}
.headerBarWrapper .headerBarRight .AvatarImg span {
  height: 26px;
  line-height: 26px;
}
.headerBarWrapper .headerBarRight .userName:hover {
  text-decoration: underline;
}
.headerBarWrapper .headerBarRight .userName {
  margin-left: 9px;
  margin-right: 16px;
  font-size: 14px;
  font-family: "NotoSansHans-Regular", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  cursor: pointer;
}
.headerBarWrapper .headerBarRight .LogInBt {
  width: 70px;
  height: 25px;
  line-height: 21px;
  border: 2px solid #000000;
  border-radius: 20px;
  font-family: "NotoSansHans-Regular", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 16px;
  color: #000000;
  cursor: pointer;
  text-align: center;
}
.headerBarWrapper .headerBarRight .Logout,
.headerBarWrapper .headerBarRight .SignUp {
  height: 28px;
  width: 84px;
  line-height: 14px;
  font-size: 14px;
  padding: 14px 3px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 1.5px solid #FFE9C2;
  border-radius: 21.5px;
  white-space: nowrap;
  font-family: "NotoSansHans-Regular", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  cursor: pointer;
}
@media screen and (min-width: 524px) {
  .ant-layout-header {
    padding: 0px 30px !important;
  }
}
@media screen and (max-width: 524px) {
  .ant-layout-header {
    padding: 0px 10px !important;
  }
  .headerBarRight div:nth-child(2),
  .headerBarRight div:nth-child(3) {
    display: none;
  }
}
