@font-face {
  font-family: "NotoSansHans-Bold";
  src: url(/static/media/NotoSansHans-Bold1.1a1d2b4f.ttf), url(/static/media/NotoSansHans-Bold.be1ab001.eot);
}
@font-face {
  font-family: "NotoSansHans-Regular";
  src: url(/static/media/NotoSans-Regular1.0c22b1d8.ttf), url(/static/media/NotoSansHans-Regular.127a1713.eot);
}
@font-face {
  font-family: "NotoSansHans-Medium";
  src: url(/static/media/NotoSansHans-Medium.1d9e1675.ttf), url(/static/media/NotoSansHans-Medium.e257c7c1.eot);
}
.generated-view-dox {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  padding: 15px 0px;
}
.generated-view-dox .FuntionView {
  margin-bottom: 17px;
  -webkit-column-gap: 11px;
  /* Chrome, Safari, Opera */
  -moz-column-gap: 11px;
  /* Firefox */
  grid-column-gap: 11px;
  column-gap: 11px;
  position: relative;
}
.generated-view-dox .FuntionView .download-box {
  position: absolute;
  margin-top: -50px;
  width: 100%;
}
.generated-view-dox span {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-family: "NotoSansHans-Medium", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 16px;
  color: #000000;
  letter-spacing: 0;
  text-align: center;
  margin-bottom: 15px;
}
.generated-view-dox .fileTypeAndNameView {
  display: flex;
  flex: 1 1;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.generated-view-dox .fileType-icon {
  width: 123px;
  height: 79px;
  margin-bottom: 10px;
}
.button-box:hover {
  border: #FCBE31 4px solid;
}
.button-box {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 140px;
  height: 45px;
  background-color: #064969;
  cursor: pointer;
}
.button-box div {
  width: 21px;
  height: 21px;
  margin-right: 11px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}
.button-box p {
  font-family: "NotoSansHans-Medium", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 16px;
  color: #FFFFFF;
  letter-spacing: 0;
  text-align: center;
  margin-bottom: 0px;
}
@media screen and (max-width: 550px) {
  .button-box {
    width: 100px !important;
    height: 40px;
  }
  .button-box div {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
    min-width: 21px;
    margin-right: 5px !important;
  }
  .button-box p {
    font-size: 14px !important;
  }
}
