@font-face {
  font-family: "NotoSansHans-Bold";
  src: url(/static/media/NotoSansHans-Bold1.1a1d2b4f.ttf), url(/static/media/NotoSansHans-Bold.be1ab001.eot);
}
@font-face {
  font-family: "NotoSansHans-Regular";
  src: url(/static/media/NotoSans-Regular1.0c22b1d8.ttf), url(/static/media/NotoSansHans-Regular.127a1713.eot);
}
@font-face {
  font-family: "NotoSansHans-Medium";
  src: url(/static/media/NotoSansHans-Medium.1d9e1675.ttf), url(/static/media/NotoSansHans-Medium.e257c7c1.eot);
}
.generated-view-dox {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  padding: 15px 0px;
}
.generated-view-dox .FuntionView {
  margin-bottom: 17px;
  -webkit-column-gap: 11px;
  /* Chrome, Safari, Opera */
  -moz-column-gap: 11px;
  /* Firefox */
  grid-column-gap: 11px;
  column-gap: 11px;
  position: relative;
}
.generated-view-dox .FuntionView .download-box {
  position: absolute;
  margin-top: -50px;
  width: 100%;
}
.generated-view-dox span {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-family: "NotoSansHans-Medium", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 16px;
  color: #000000;
  letter-spacing: 0;
  text-align: center;
  margin-bottom: 15px;
}
.generated-view-dox .fileTypeAndNameView {
  display: flex;
  flex: 1 1;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.generated-view-dox .fileType-icon {
  width: 123px;
  height: 79px;
  margin-bottom: 10px;
}
.toFactsheet .fileType-icon {
  background: url("http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/download/factsheetIcon.png") no-repeat top;
  background-size: contain;
}
.toFactsheet .spining-center-box {
  position: absolute;
  top: 50%;
  width: 100%;
  height: 100px;
  display: flex;
  flex-direction: column;
}
